import React, { useEffect, useState } from 'react'

import axios from "axios";
import { NavBar } from '../ui/NavBar';
import { ProductCard } from './ProductCard';
import { Link } from 'react-router-dom';

import Select from "react-select";

import "../../styles/productos/productos.css";
import { deleteProductoAdmin } from '../../actions/DeleteProduct';

export const ProductScreen = () => {

    const usersApi = axios.create();
    const uid = localStorage.getItem("uid");
    const token = localStorage.getItem("token");



    const [productos, setProductos] = useState([]);

       
    usersApi.get('http://localhost:4000/admin/api/', {
    headers: {
        'x-token': token
    }
})
    .then((res) => {
        
        setProductos(res.data.productos);
        
    })

    console.log(productos)
    
    
    
    const [nombre, setNombre] = useState("")
    const [stock, setStock] = useState("")
    
    const [categoriaGSelect, setcategoriaGSelect] = useState({
        value:"",
        label:""
    });
    
    const [categoriaSelect, setCategoriaSelect] = useState({
        value:"",
        label:""
    })
    
    const handleCatGeChange = (selectedOption) => {
        
        
        setcategoriaGSelect(selectedOption);
        
    };   
    
    const handleCatChange = (selectedOption)=>{
        

        setCategoriaSelect(selectedOption);
    };
    
    const filteredMerca = () =>{
        
        
        
        if ( categoriaGSelect.value.length===0 && nombre.length===0 && stock.length===0 && categoriaSelect.value.length===0 ){
             return productos
            }
            
         else if (categoriaGSelect.value.length===0 && nombre.length!==0 && stock.length!==0 && categoriaSelect.value.length===0){
             const productosFiltrados= productos.filter(merca=>{
                 
                 let stockP;
                 if (merca.stock.toString()!==stock){
                     stockP=false
                    }else{
                        stockP=true
                        
                        
                    }
                    
                    
    
                    return ((merca.nombre.includes(nombre)) && (stockP) )
            })
            
            return productosFiltrados
            
        }
        else if (categoriaGSelect.value.length===0 && nombre.length!==0 && stock.length!==0 && categoriaSelect.value.length!==0){
            const productosFiltrados= productos.filter(merca=>{
                
                let stockP;
                if (merca.stock.toString()!==stock){
                    stockP=false
                   }else{
                       stockP=true
                       
                       
                   }
                
                   console.log(categoriaSelect.value==merca.categoria[0])
                   
   
                   return ((merca.nombre.includes(nombre)) && (stockP)&& (categoriaSelect.value==merca.categoria[0]))
           })
           
           return productosFiltrados
           
       }
        else if (categoriaGSelect.value.length!==0 && nombre.length===0 && stock.length!==0 
            && categoriaSelect.value.length===0
            ){
            const productosFiltrados= productos.filter(merca=>{
              
                let stockP;
                if (merca.stock!==stock){
                    stockP=false
                }else{
                    stockP=true
    
    
                }
    
                
    
                return ((merca.categoriaGeneral=categoriaGSelect.value) && (stockP) )
            })
            return productosFiltrados
        } 
        
        else if (categoriaGSelect.value.length!==0 && nombre.length===0 && stock.length!==0 
            && categoriaSelect.value.length!==0
            ){
            const productosFiltrados= productos.filter(merca=>{
              
                let stockP;
                if (merca.stock!==stock){
                    stockP=false
                }else{
                    stockP=true
    
    
                }
    
                
    
                return ((merca.categoriaGeneral==categoriaGSelect.value) && (stockP) &&(categoriaSelect.value==merca.categoria[0])  )
            })
            return productosFiltrados
        } 
        else if (categoriaGSelect.value.length!==0 && nombre.length!==0 && stock.length===0 && categoriaSelect.value.length===0 ){
            const productosFiltrados= productos.filter(merca=>{
            
    
              
                return (merca.categoriaGeneral==categoriaGSelect.value) && (merca.nombre.includes(nombre))
            })
            return productosFiltrados
        }
        else if (categoriaGSelect.value.length!==0 && nombre.length!==0 && stock.length===0 && categoriaSelect.value.length!==0 ){
            const productosFiltrados= productos.filter(merca=>{
            
    
              
                return (merca.categoriaGeneral==categoriaGSelect.value) && (merca.nombre.includes(nombre) && (categoriaSelect.value==merca.categoria[0]))
            })
            return productosFiltrados
        }
        else if (categoriaGSelect.value.length!==0 && nombre.length===0 && stock.length===0 && categoriaSelect.value.length===0){
            const productosFiltrados= productos.filter(merca=>{
              
                
    
                return (  merca.categoriaGeneral==categoriaGSelect.value )
            })
            return productosFiltrados
        }

        else if (categoriaGSelect.value.length!==0 && nombre.length===0 && stock.length===0 && categoriaSelect.value.length!==0){
            const productosFiltrados= productos.filter(merca=>{
              
                
    
                return (  merca.categoriaGeneral==categoriaGSelect.value  && categoriaSelect.value==merca.categoria[0])
            })
            return productosFiltrados
        }
        

        else if (categoriaGSelect.value.length!==0 && nombre.length===0 && stock.length!==0 && categoriaSelect.value.length===0 ){
            const productosFiltrados= productos.filter(merca=>{
              
                let stockP;
                if (merca.stock!==stock){
                    stockP=false
                }else{
                    stockP=true
    
    
                }
    
                
    
                return ((merca.categoriaGeneral==categoriaGSelect.value)&& (stockP) )
            })
            return productosFiltrados
        }

        else if (categoriaGSelect.value.length!==0 && nombre.length===0 && stock.length!==0 && categoriaSelect.value.length!==0 ){
            const productosFiltrados= productos.filter(merca=>{
              
                let stockP;
                if (merca.stock!==stock){
                    stockP=false
                }else{
                    stockP=true
    
    
                }
    
                
    
                return ((merca.categoriaGeneral==categoriaGSelect.value)&& (stockP)&& (categoriaSelect.value==merca.categoria[0]) )
            })
            return productosFiltrados
        }

        else if (categoriaGSelect.value.length!==0 && nombre.length!==0 && stock.length===0 && categoriaSelect.value.length===0){
            const productosFiltrados= productos.filter(merca=>{
               
    
                return ((merca.categoriaGeneral==categoriaGSelect.value) && (merca.nombre.includes(nombre))  )
            })
            return productosFiltrados
        }
        else if (categoriaGSelect.value.length!==0 && nombre.length!==0 && stock.length===0 && categoriaSelect.value.length!==0){
            const productosFiltrados= productos.filter(merca=>{
               
    
                return ((merca.categoriaGeneral==categoriaGSelect.value) && (merca.nombre.includes(nombre)) && (categoriaSelect.value==merca.categoria[0]) )
            })
            return productosFiltrados
        }
        else if (categoriaGSelect.value.length===0 && nombre.length!==0 && stock.length ===0 && categoriaSelect.value.length===0){
            const productosFiltrados= productos.filter(merca=>{
              
    
                return ((merca.nombre.includes(nombre)))
            })
            return productosFiltrados
        }
        else if (categoriaGSelect.value.length===0 && nombre.length!==0 && stock.length ===0 && categoriaSelect.value.length!==0){
            const productosFiltrados= productos.filter(merca=>{
              
    
                return ((merca.nombre.includes(nombre)) && (categoriaSelect.value== merca.categoria[0]))
            })
            return productosFiltrados
        }
        else if (nombre.length===0 && stock.length===0 && categoriaGSelect.value.length===0 && categoriaSelect.value.length!==0){

            const productosFiltrados=productos.filter(merca=>{

                return (merca.categoria[0]==categoriaSelect.value)
            })

            return productosFiltrados
        }



        else{
            const productosFiltrados= productos.filter(merca=>{
              
                let stockP;
                if (merca.stock!==stock){
                    stockP=false
                }else{
                    stockP=true
    
    
                }
    
                
    
                return ((merca.categoriaGeneral==categoriaGSelect.value)&&(merca.nombre.includes(nombre)) && (stockP) )
            })
            return productosFiltrados
        
        
        }}

        
    

   
        
    
    
    const handleCategoriaChange =(e) =>{
        e.preventDefault();
        setNombre(e.target.value)
    }
    
    const handlePrecioChange = (e) =>{
        e.preventDefault();
        setStock(e.target.value)
        
    }
    const optionsCategoriaGeneral = productos.map(i=>{

        if(categoriaSelect.value===""){
            return({
                "label":i.categoriaGeneral,
                "value":i.categoriaGeneral
            })
        }
        else{
            if(categoriaSelect.value===i.categoria[0]){
                return({
                    "label":i.categoriaGeneral,
                    "value":i.categoriaGeneral
                })
            }
        }
        return({
            "label":i.categoriaGeneral,
            "value":i.categoriaGeneral
        })
        
        
    })
    
    optionsCategoriaGeneral[0]={"label":"Categoria General","value":""}
    
    
    
    const optionsCategoria = productos.map(i=>{
        
        if(categoriaGSelect.value===""){
            return({
                "label":i.categoria[0],
                "value":i.categoria[0]
            })
        }
        else{
            if (categoriaGSelect.value===i.categoriaGeneral){
                
                return ({
                    "label" : i.categoria[0],
                    "value" : i.categoria[0],
                    
                    
                })
                
            }
            
            
            
            
        }
        
        return ({
            "label" : i.categoria[0],
            "value" : i.categoria[0],
            
            
        })
        
    })
    optionsCategoria[0]={"label":"Categoria","value":""}
    
    const eliminarProductoAdmin= (id)=>{
        
        deleteProductoAdmin(id);
    }
      
    return (
        <>





            <NavBar/>
        <div className="container">
            
            <h1>ProductScreen</h1>
   
            <hr/>

            <div>
                <h1>
                   
                </h1>
            </div>

            <div>

                <span>Nombre:</span> <input type="text" value={nombre} name="Categoria"
                onChange={handleCategoriaChange}/>
                
                <span>Stock:</span> <input type="number" value={stock} name="Precio"
                onChange={handlePrecioChange}/>

            </div>


            <div>
                <button className="btn btn-danger">
                    Anterior
                </button>
                <button className="btn btn-primary">
                    Siguiente

                </button>

            </div>

            <div>

                <Link to="/admin-api/productos/crear-producto" >
                    Crear+
                </Link>

            </div>

            <div>

            <div className="row">

            <Select className="col-6" placeholder="Categoria General"
                value={optionsCategoriaGeneral.value}
                onChange={handleCatGeChange}
                options={optionsCategoriaGeneral}
                
            />

            <Select className="col-6" placeholder="Categoria"
                value={optionsCategoria.value}
                onChange={handleCatChange}
                options={optionsCategoria}
                
                />
                        
            </div>
            


            </div>

            <hr/>



            <div className="row m-3">

                <table>
                <tr>
                    <td> Nombre: </td>
                    <td> Categoria General: </td>
                    <td> Categoria: </td>
                    <td> Precio x mayor: </td>
                    <td> Precio: </td>
                    <td> Descripcion: </td>
                    <td> Imagen: </td>

                    <td>Opciones:</td>
                    
                </tr>
                {
                    filteredMerca().map(i=>{
                        return (
                            <tr className="fila-tabla-productos">
                                
                               <td> 
                                   <Link to={`/admin-api/productos/ver/${i.id}`}>
                                   {i.nombre}
                                   </Link>
                                </td> 
                               <td>{i.categoriaGeneral}</td> 
                               <td>{i.categoria}</td> 
                               <td>{i.precioMayor}</td> 
                               <td>{i.precio}</td> 
                               <td>{i.descripcion}</td> 
                               <td><img width="50px" height="50px"
                               src={i.colores[0].urlImagenes[0]}/></td>

                               <td><button onClick={()=>eliminarProductoAdmin(i.id)} className="btn btn-danger">Eliminar Producto</button></td>
                                
                            </tr>
                                
                        )
                    })
                }

                            
                </table>
                               
            </div>

        </div>

        </>
    )


}
                               





